@import url('./input.css');
.logo-img {
  width: 100px;
  height: 100px;
}

.chromium-logo {
  width: 104px;
  height: 104px;
}

.explains .detail {
  font-style: italic;
  color: #888;
}

.go-top {
  position: fixed;
  right: 200px;
  bottom: 10px;
  cursor: pointer;
  border: none;
  background-color: rgb(30, 162, 30);
  color: #fff;
  min-width: 125px;
  border-radius: 5px;
  padding: 0.4rem 0.5rem;
  font-weight: 600;
  font-size: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.select-class {
  width: 400px;
}

.ver-pos-tips {
  font-style: italic;
  color: #aaa;
  margin: 10px 0;
}
.vir-list-wrapper {
  padding: 5px;
  border: 1px solid #ddd;
  width: 400px;
}

.ver-pos-row {
  position: relative;
  margin: 2px;
}
.ver-pos-row a {
  display: flex;
  text-decoration: none;
}
